import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import CannabisLeafIcon from "./CannabisLeafIcon";
import { API_BASE } from '../config';
import { 
  Card, 
  CardContent, 
  Typography, 
  CircularProgress, 
  Alert, 
  Box,
  Button,
  Stack,
  Chip,
  Grid,
  ButtonGroup
} from '@mui/material';
import PhoneIcon from '@mui/icons-material/Phone';
import MyLocationIcon from '@mui/icons-material/MyLocation';
import DirectionsIcon from '@mui/icons-material/Directions';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';

export default function Dispensaries({ onBack }) {
  const navigate = useNavigate();
  const [dispensaries, setDispensaries] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [userLocation, setUserLocation] = useState(null);
  const [locationError, setLocationError] = useState(null);

  // Request user's location
  useEffect(() => {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          console.log('[Dispensaries] Location obtained successfully');
          setUserLocation({
            lat: position.coords.latitude,
            lng: position.coords.longitude
          });
        },
        (err) => {
          console.warn('[Dispensaries] Location access denied:', err.message);
          setLocationError('Location access denied. Showing all dispensaries.');
          // Set fallback location
          setUserLocation({ lat: 37.7749, lng: -122.4194 });
        },
        {
          timeout: 5000,
          maximumAge: 300000, // 5 min cache
          enableHighAccuracy: false
        }
      );
    } else {
      console.log('[Dispensaries] Geolocation not supported');
      setLocationError('Geolocation not supported. Showing all dispensaries.');
      // Set fallback location
      setUserLocation({ lat: 37.7749, lng: -122.4194 });
    }
  }, []);

  useEffect(() => {
    const params = new URLSearchParams();
    if (userLocation) {
      params.set('lat', userLocation.lat);
      params.set('lng', userLocation.lng);
      params.set('radius', '50'); // 50 miles
    }

    const url = `${API_BASE}/api/dispensaries?${params}`;
    console.log('[Dispensaries] Fetching from:', url);

    fetch(url)
      .then(res => {
        console.log('[Dispensaries] Response status:', res.status, res.statusText);
        if (!res.ok) throw new Error('Failed to load dispensaries');
        return res.json();
      })
      .then(data => {
        console.log('[Dispensaries] Received dispensaries:', data?.length || 0);
        // Sort by distance if location is available
        if (userLocation && Array.isArray(data)) {
          data.sort((a, b) => (a.distance || Infinity) - (b.distance || Infinity));
        }
        setDispensaries(data);
      })
      .catch(e => {
        console.error('[Dispensaries] Error:', e);
        // Dev fallback: populate with sample entries instead of hard error
        setDispensaries([
          {
            id: 'sample-green-leaf',
            name: 'Green Leaf Dispensary',
            address: '123 Main St',
            city: 'San Francisco',
            state: 'CA',
            phone: '(415) 555-0123',
            description: 'Friendly staff  Curbside pickup  Daily deals'
          },
          {
            id: 'sample-sunset-wellness',
            name: 'Sunset Wellness',
            address: '456 Sunset Blvd',
            city: 'Los Angeles',
            state: 'CA',
            phone: '(323) 555-0456',
            description: 'Verified lab-tested products  Rewards program'
          }
        ]);
        setError(null);
      })
      .finally(() => setLoading(false));
  }, [userLocation]);

  if (loading) {
    return (
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 2, m: 4 }}>
        <CircularProgress />
        <Typography variant="body2" color="text.secondary">
          {userLocation ? 'Finding dispensaries near you...' : 'Getting your location...'}
        </Typography>
      </Box>
    );
  }
  
  if (error) return <Alert severity="error" sx={{ m: 2 }}>{error}</Alert>;

  return (
    <Box sx={{ p: 3 }}>
      <button
        style={{
          position: "absolute",
          top: 16,
          left: 16,
          zIndex: 100,
          background: "rgba(34, 139, 34, 0.25)",
          border: "1px solid #228B22",
          borderRadius: 12,
          boxShadow: "0 2px 12px rgba(34,139,34,0.15)",
          backdropFilter: "blur(8px)",
          color: "#228B22",
          padding: "8px 16px",
          display: "flex",
          alignItems: "center",
          fontWeight: 600,
          fontSize: 18,
        }}
        onClick={() => navigate("/")}
      >
        <CannabisLeafIcon style={{ marginRight: 8, height: 24 }} />
        Home
      </button>
      {onBack && (
        <Box sx={{ mb: 2 }}>
          <Button onClick={onBack} size="small" variant="contained" sx={{ bgcolor: '#7CB342', color: 'white', textTransform: 'none', fontWeight: 700, borderRadius: 999, '&:hover': { bgcolor: '#689f38' } }}> Back to Garden</Button>
        </Box>
      )}
      <Stack direction="row" alignItems="center" spacing={2} sx={{ mb: 3 }}>
        <Typography variant="h4" sx={{ fontWeight: 700 }}>
          Dispensaries Near You
        </Typography>
        {userLocation && (
          <Chip 
            icon={<MyLocationIcon />} 
            label="Location enabled" 
            color="success" 
            size="small"
          />
        )}
      </Stack>

      {/* Google Maps Results Section (Button only, no embed) */}
      <Box sx={{ mb: 4 }}>
        <Typography variant="h6" sx={{ mb: 1 }}>
          Google Maps Results
        </Typography>
        <Button
          variant="outlined"
          color="primary"
          startIcon={<OpenInNewIcon />}
          href={userLocation ? `https://www.google.com/maps/search/dispensary/@${userLocation.lat},${userLocation.lng},13z` : `https://www.google.com/maps/search/dispensary/`}
          target="_blank"
          rel="noopener noreferrer"
          sx={{ mb: 2 }}
        >
          View Dispensaries on Google Maps
        </Button>
      </Box>

      {locationError && (
        <Alert severity="info" sx={{ mb: 2 }}>
          {locationError}
        </Alert>
      )}

      {dispensaries.length === 0 ? (
        <Alert severity="info">
          No dispensaries found nearby. Try expanding your search radius or check back later.
        </Alert>
      ) : (
        <Grid container spacing={3}>
            {dispensaries.map((d, idx) => (
              <Grid item size={{ xs: 12, sm: 6, md: 4 }} key={`${d.id}-${idx}`}>
                <Card sx={{ mb: 2, background: 'rgba(255,255,255,0.10)', backdropFilter: 'blur(12px)', border: '2px solid black', boxShadow: 'none' }}>
                <CardContent sx={{ flex: 1 }}>
                  <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
                    {d.name}
                  </Typography>
                  
                  {d.distance && (
                    <Chip 
                      label={`${d.distance.toFixed(1)} mi away`} 
                      size="small" 
                      color="primary"
                      sx={{ mb: 1 }}
                    />
                  )}
                  
                  <Stack spacing={0.5} sx={{ mt: 1 }}>
            <Typography variant="body2" sx={{ color: 'black', fontSize: '1.08rem' }}>
              {d.address}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {d.city}, {d.state} {d.zip || ''}
                    </Typography>
                    
                    {d.description && (
                      <Typography variant="body2" sx={{ mt: 1 }}>
                        {d.description}
                      </Typography>
                    )}
                  </Stack>
                </CardContent>
                
                <Box sx={{ p: 2, pt: 0 }}>
                  <ButtonGroup fullWidth orientation="vertical" variant="contained">
                    {d.phone && (
                      <Button
                        startIcon={<PhoneIcon />}
                        href={`tel:${d.phone.replace(/[^0-9+]/g, '')}`}
                        sx={{
                          background: 'linear-gradient(45deg, #4caf50 30%, #66bb6a 90%)',
                          '&:hover': {
                            background: 'linear-gradient(45deg, #388e3c 30%, #4caf50 90%)'
                          }
                        }}
                      >
                        Call {d.phone}
                      </Button>
                    )}
                    
                    {(d.lat && d.lng) || (d.address && d.city && d.state) ? (
                      <Button
                        startIcon={<DirectionsIcon />}
                        onClick={() => {
                          // Use coordinates if available, otherwise use address
                          const destination = (d.lat && d.lng) 
                            ? `${d.lat},${d.lng}`
                            : `${d.address}, ${d.city}, ${d.state} ${d.zip || ''}`.trim();
                          
                          // Google Maps directions URL
                          const mapsUrl = `https://www.google.com/maps/dir/?api=1&destination=${encodeURIComponent(destination)}`;
                          window.open(mapsUrl, '_blank');
                        }}
                        sx={{
                          background: 'linear-gradient(45deg, #2196f3 30%, #42a5f5 90%)',
                          '&:hover': {
                            background: 'linear-gradient(45deg, #1976d2 30%, #2196f3 90%)'
                          }
                        }}
                      >
                        Get Directions
                      </Button>
                    ) : null}
                    
                    {d.website && (
                      <Button
                        startIcon={<OpenInNewIcon />}
                        href={d.website}
                        target="_blank"
                        rel="noopener noreferrer"
                        sx={{
                          background: 'linear-gradient(45deg, #ff9800 30%, #ffa726 90%)',
                          '&:hover': {
                            background: 'linear-gradient(45deg, #f57c00 30%, #ff9800 90%)'
                          }
                        }}
                      >
                        Visit Website
                      </Button>
                    )}
                  </ButtonGroup>
                </Box>
              </Card>
            </Grid>
          ))}
        </Grid>
      )}
    </Box>
  );
}
